import "./Home.css";
import "../Projects/Project.css";
import "../About/About.css";
import Profile from "./Profile.jpg";
import Iframe from 'react-iframe'
import GitHubCalendar from 'react-github-calendar';
import bulb from './lightbulb.png'
import tictac from './tic-tac-toe.png';
import calculator from './calculator.png'
import gmail from './gmail.png'

function Home() {
    return (
        <div className="Home">
            {/*Body Of Main Home Page*/}
            <div className="Body">
                {/*Introduction*/}
                <div className="intro-container">
                    <div className="intro">
                        <div className="para">
                            <p className="para1">
                                Hi I'm Tanisha Rao! 🙋🏻‍♀️
                            </p>
                            <p className="para2">
                                <p> 😍 I absolutely love making websites and apps.</p>
                                <p>👩🏻‍💻 Excited to connect with cool people who share the same coding vibes!</p>
                                <p>🪄✨ Happy Coding! ✨🪄</p>
                            </p>
                        </div>
                        <div className="photo">
                            <img src={Profile} className="p-photo" alt="my profile"></img>
                        </div>
                    </div>
                </div>
                <div className="aboutMeContainer">
                    {/* Small Timeline */}
                    <div className="aboutMe">
                        <div>
                            <h2 className="left topic">ABOUT ME</h2>
                        </div>
                        <hr className="hr1" />
                        <div className="timeline-block">
                            <div className="timeline ">
                                <div className="container right">
                                    <div className="content">
                                        <p className="about-year">2023</p>
                                        <p className="about-degree">University graduation in Computer Application</p>
                                        <p className="about-school">Delhi Global Institute of Technology</p>
                                        <p className="about-board">Maharshi Dayanand University</p>
                                    </div>
                                </div>
                                <div className="container right">
                                    <div className="content">
                                        <p className="about-year">2020</p>
                                        <p className="about-degree">Senior School Graduation</p>
                                        <p className="about-school">Happy Model School</p>
                                        <p className="about-board">CBSE</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/*Starred Projects*/}
                <div className="projectsMain">
                    <h2 className="left topic">PROJECTS</h2>
                    <hr className="hr1" />

                    <div className="divproject">

                        <div className="card">
                            <div>
                                <Iframe className="iframediv" url="https://switchbutton.vercel.app/" title="My Site"></Iframe>
                                <img src={bulb} className='project-photo'></img>
                            </div>
                            <a href="https://switchbutton.vercel.app/" className="p-name">
                                <p >Switch Button</p>
                                {/* add star  */}
                            </a>
                            <div className="codeyear">
                                <a href='https://github.com/Tanisharao2002/switchButton' className="code">
                                    <p >Code 🔗</p>
                                </a>
                                <p className="year">2023</p>
                            </div>
                            <p className="bio">Project to switch on and off using JS</p>
                        </div>
                        <div className="card">
                            <div>
                                <Iframe className="iframediv" url="https://tanisharao2002.github.io/tictactoe/" title="My Site"></Iframe>
                                <img src={tictac} className='project-photo'></img>
                            </div>
                            <a href="https://tanisharao2002.github.io/tictactoe/" className="p-name">
                                <p >Tic Tac Toe</p>
                                {/* add star  */}
                            </a>
                            <div className="codeyear">
                                <a href='https://github.com/Tanisharao2002/tictactoe' className="code">
                                    <p >Code 🔗</p>
                                </a>
                                <p className="year">2023</p>
                            </div>
                            <p className="bio">Simple Tic Tac Toe Game using ReactJS</p>
                        </div>
                        <div className="card">
                            <div>
                                <Iframe className="iframediv" url="https://tanisharao2002.github.io/calculator/" title="My Site"></Iframe>
                                <img src={calculator} className='project-photo'></img>
                            </div>
                            <a href="https://tanisharao2002.github.io/calculator/" className="p-name">
                                <p >Calculator</p>
                                {/* add star  */}
                            </a>
                            <div className="codeyear">
                                <a href='https://github.com/Tanisharao2002/calculator' className="code">
                                    <p >Code 🔗</p>
                                </a>
                                <p className="year">2023</p>
                            </div>
                            <p className="bio">Calculator using ReactJS.</p>
                        </div>
                        <div className="card">
                            <div>
                                <Iframe className="iframediv" url="https://tanisharao2002.github.io/Login-Page" title="My Site"></Iframe>
                                <img src={gmail} className='project-photo'></img>
                            </div>
                            <a href="https://tanisharao2002.github.io/Login-Page" className="p-name">
                                <p >Google Mail Login Page Clone</p>
                                {/* add star  */}
                            </a>
                            <div className="codeyear">
                                <a href='https://github.com/Tanisharao2002/Login-Page' className="code">
                                    <p >Code 🔗</p>
                                </a>
                                <p className="year">2023</p>
                            </div>
                            <p className="bio">Gmail Login Page using ReactJS.</p>
                        </div>

                    </div>



                </div>


                {/* Github Progress Chart */}
                <div className="progress">
                    <div>
                        <h2 className="left topic">MY PROGRESS</h2>
                        <hr className="hr1" />
                    </div>
                    <div className="calendar-container">
                        <div className="chart1">
                            <GitHubCalendar username="tanisharao2002" colorScheme="light" />
                        </div>
                    </div>
                    {/* <div class="counter">
                    <div>Website visit count:</div>
                    <div class="website-counter">
                        <p className="count" onLoad={Count}></p>
                    </div>
                    </div>*/}
                </div>
            </div>



            {/* <div className="overlay-button">
            <img src={Arrow} className="overlayArrow"></img>

            </div> */}
        </div>
    )
}

export default Home;


