import './Contact.css';
import Insta from './instagram.png'
import LinkedIn from './linkedin.png'
import Github from './social.png'
import Twitter from './twitter.png'
import GMail from './gmail.png'
import ContactMe from './contactMe.png'


function Contact() {
    return (
        
            <div className='ContactMe'>
                <div className='contactMeImg'>
                    <img src={ContactMe} className='contact-photo' title=""></img>
                </div>
                <div className="contact-container">
                    <a href='https://www.instagram.com/tanisha.rao_/'  target='_blank'>
                        <div className="contact">

                            <img src={Insta} className="contactImg" title=""/>
                            {/* <p className="contactP"></p> */}
                        </div>
                    </a>
                    <a href='https://www.linkedin.com/in/tanisha911' target='_blank'>
                        <div className="contact">
                            <img src={LinkedIn} className="contactImg" title=""/>

                        </div>
                    </a>
                    <a href='https://github.com/Tanisharao2002' target='_blank'>
                        <div className="contact">
                            <img src={Github} className="contactImg" title=""/>

                        </div>
                    </a>
                    <a href='https://twitter.com/tanisharao20' target='_blank'>
                        <div className="contact">
                            <img src={Twitter} className="contactImg" title=""/>

                        </div>
                    </a>
                    <a href='mailto:tanisharao810@gmail.com' target='_blank'>
                        <div className="contact">
                            <img src={GMail} className="contactImg" title=""/>

                        </div>
                    </a>
                </div>
           
        </div>
    )
}

export default Contact;